"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  MagnifyingGlassIcon,
  ArrowPathIcon,
  Squares2X2Icon,
  Bars3BottomLeftIcon,
  ViewColumnsIcon,
  DocumentIcon,
  PhotoIcon,
  ArchiveBoxIcon,
} from "@heroicons/react/24/outline";

/* =======================
   Types
======================= */

type RiskLevel = "HIGH" | "MEDIUM" | "LOW";
type ViewMode = "grid" | "list" | "detail";

interface ComparisonItem {
  id: number;
  document_name: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: string | null;
}

interface DocumentCard {
  id: string;
  name: string;
  fileType: string;
  latestVersion: string;
  lastUpdated: string;
  totalComparisons: number;
  risk: RiskLevel;
  fileSize: string;
}

/* =======================
   Config
======================= */

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() ||
  "http://127.0.0.1:8000";

const riskRank: Record<RiskLevel, number> = {
  HIGH: 3,
  MEDIUM: 2,
  LOW: 1,
};

function normalizeRisk(r?: string | null): RiskLevel {
  const x = (r || "").toUpperCase();
  if (x.includes("HIGH")) return "HIGH";
  if (x.includes("MEDIUM")) return "MEDIUM";
  return "LOW";
}

function formatDate(iso: string) {
  try {
    return new Date(iso).toLocaleDateString("th-TH", {
      day: "numeric",
      month: "short",
      year: "numeric",
    });
  } catch {
    return iso;
  }
}

function getFileType(name: string) {
  const ext = name.split(".").pop()?.toLowerCase();
  if (ext === "pdf") return "PDF";
  if (["doc", "docx"].includes(ext || "")) return "WORD";
  if (["xls", "xlsx"].includes(ext || "")) return "EXCEL";
  if (["jpg", "png"].includes(ext || "")) return "IMAGE";
  if (["zip", "rar"].includes(ext || "")) return "ARCHIVE";
  return "FILE";
}

function randomFileSize() {
  const mb = (Math.random() * 8 + 0.2).toFixed(1);
  return `${mb} MB`;
}

/* =======================
   File Icon (‡πÄ‡∏´‡∏°‡∏∑‡∏≠‡∏ô‡πÉ‡∏ô‡∏£‡∏π‡∏õ)
======================= */

function FileIcon({ type }: { type: string }) {
  const base =
    "relative w-12 h-16 rounded-md border bg-white clip-path-fold";

  const labelStyle =
    "absolute bottom-1 left-1/2 -translate-x-1/2 text-[10px] font-bold";

  if (type === "PDF") {
    return (
      <div className={`${base} border-red-300`}>
        <div className="file-fold-corner" />
        <span className={`${labelStyle} text-red-600`}>PDF</span>
      </div>
    );
  }

  if (type === "WORD") {
    return (
      <div className={`${base} border-blue-300`}>
        <div className="file-fold-corner" />
        <span className={`${labelStyle} text-blue-600`}>DOC</span>
      </div>
    );
  }

  if (type === "EXCEL") {
    return (
      <div className={`${base} border-green-300`}>
        <div className="file-fold-corner" />
        <span className={`${labelStyle} text-green-600`}>XLS</span>
      </div>
    );
  }

  if (type === "IMAGE") {
    return <PhotoIcon className="h-10 w-10 text-purple-500" />;
  }

  if (type === "ARCHIVE") {
    return <ArchiveBoxIcon className="h-10 w-10 text-yellow-600" />;
  }

  return <DocumentIcon className="h-10 w-10 text-gray-500" />;
}

/* =======================
   Page
======================= */

export default function DocumentsPage() {
  const [data, setData] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");
  const [view, setView] = useState<ViewMode>("detail");

  useEffect(() => {
    fetchData();
  }, []);

  async function fetchData() {
    setLoading(true);
    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=300`);
      const json = await res.json();
      setData(Array.isArray(json) ? json : []);
    } finally {
      setLoading(false);
    }
  }

  /* =======================
     Build Documents
  ======================= */
  const documents = useMemo<DocumentCard[]>(() => {
    const map = new Map<string, DocumentCard>();

    data.forEach((c) => {
      const name = c.document_name || "Untitled";
      const type = getFileType(name);

      if (!map.has(name)) {
        map.set(name, {
          id: encodeURIComponent(name),
          name,
          fileType: type,
          latestVersion: c.version_new_label || "-",
          lastUpdated: c.created_at,
          totalComparisons: 0,
          risk: normalizeRisk(c.overall_risk_level),
          fileSize: randomFileSize(),
        });
      }

      const d = map.get(name)!;
      d.totalComparisons++;
      if (new Date(c.created_at) > new Date(d.lastUpdated)) {
        d.lastUpdated = c.created_at;
        d.latestVersion = c.version_new_label || d.latestVersion;
      }

      const r = normalizeRisk(c.overall_risk_level);
      if (riskRank[r] > riskRank[d.risk]) d.risk = r;
    });

    return Array.from(map.values());
  }, [data]);

  const filtered = documents.filter((d) =>
    d.name.toLowerCase().includes(search.toLowerCase())
  );

  /* =======================
     UI
  ======================= */

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white border-b">
        <div className="px-6 py-4 flex justify-between items-center">
          <h1 className="text-xl font-bold">üìÅ Documents</h1>

          <div className="flex gap-2">
            <div className="relative">
              <MagnifyingGlassIcon className="h-4 w-4 absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" />
              <input
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                placeholder="‡∏Ñ‡πâ‡∏ô‡∏´‡∏≤‡πÄ‡∏≠‡∏Å‡∏™‡∏≤‡∏£..."
                className="pl-9 pr-3 py-2 border rounded-md text-sm"
              />
            </div>

            <button
              onClick={fetchData}
              className="p-2 border rounded-md hover:bg-gray-100"
            >
              <ArrowPathIcon
                className={`h-4 w-4 ${loading ? "animate-spin" : ""}`}
              />
            </button>
          </div>
        </div>
      </div>

      {/* Toolbar */}
      <div className="bg-white border-b px-6 py-2 flex gap-2">
        <button onClick={() => setView("grid")}>
          <Squares2X2Icon className="h-5 w-5" />
        </button>
        <button onClick={() => setView("list")}>
          <Bars3BottomLeftIcon className="h-5 w-5" />
        </button>
        <button onClick={() => setView("detail")}>
          <ViewColumnsIcon className="h-5 w-5" />
        </button>
      </div>

      {/* Content */}
      <div className="p-6">
        {view === "grid" && (
          <div className="grid grid-cols-2 sm:grid-cols-4 md:grid-cols-6 gap-4">
            {filtered.map((d) => (
              <Link
                key={d.id}
                href={`/history?document_name=${encodeURIComponent(d.name)}`}
                className="bg-white border rounded-lg p-4 text-center hover:shadow file-card-hover"
              >
                <div className="flex justify-center mb-2">
                  <FileIcon type={d.fileType} />
                </div>
                <div className="text-sm font-medium truncate">{d.name}</div>
                <div className="text-xs text-gray-500 mt-1">
                  v{d.latestVersion}
                </div>
              </Link>
            ))}
          </div>
        )}

        {view === "detail" && (
          <div className="bg-white border rounded-lg overflow-hidden">
            <div className="grid grid-cols-12 px-4 py-2 text-xs text-gray-500 bg-gray-50 border-b">
              <div className="col-span-6">‡∏ä‡∏∑‡πà‡∏≠</div>
              <div className="col-span-2">‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà</div>
              <div className="col-span-2">‡∏Ç‡∏ô‡∏≤‡∏î</div>
              <div className="col-span-2">‡∏Ñ‡∏ß‡∏≤‡∏°‡πÄ‡∏™‡∏µ‡πà‡∏¢‡∏á</div>
            </div>

            {filtered.map((d) => (
              <Link
                key={d.id}
                href={`/history?document_name=${encodeURIComponent(d.name)}`}
                className="grid grid-cols-12 px-4 py-3 border-b hover:bg-blue-50"
              >
                <div className="col-span-6 flex items-center gap-3">
                  <FileIcon type={d.fileType} />
                  <div>
                    <div className="font-medium">{d.name}</div>
                    <div className="text-xs text-gray-500">
                      v{d.latestVersion} ‚Ä¢ {d.totalComparisons} comparisons
                    </div>
                  </div>
                </div>
                <div className="col-span-2 text-sm">
                  {formatDate(d.lastUpdated)}
                </div>
                <div className="col-span-2 text-sm">{d.fileSize}</div>
                <div className="col-span-2 text-sm font-semibold">
                  {d.risk}
                </div>
              </Link>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
